export default {

	// postjsonjson 
	async postJson(apiUrl, data, fromPage = "/pages/index/index") {
		let loginUser = uni.getStorageSync("loginUser");
		if (!loginUser || !loginUser.token) {
			console.log("loginUser");
			uni.redirectTo({
				url: `/pages/login/login?fromPage=${fromPage}`
			});
			return [null, ""];
		}

		let [error, res] = await uni.request({
			url: getApp().globalData.domain + apiUrl,
			method: "POST",
			data: data,
			dataType: "json",
			header: {
				"token": loginUser.token
			}
		});

		console.log(apiUrl, error, res);
		if (error || !res) {
			uni.showToast({
				title: "",
				duration: 1000,
			});
			return [null, ""];
		}

		if (res.statusCode !== 200) {
			uni.showToast({
				title: "",
				duration: 1000,
			});
			return [null, ""];
		}
		if (res.data.retcode !== 0) {
			// token
			if (res.data.retcode === 100001 || res.data.retcode === 100002) {
				console.log("token");
				uni.redirectTo({
					url: `/pages/login/login?fromPage=${fromPage}`
				});
				uni.setStorageSync("loginUser", null);
				uni.showToast({
					title: "",
					duration: 2000,
				});
				return [null, ""];
			}

			uni.showToast({
				title: "" + res.data.retcode,
				duration: 1000,
			});
			return [null, "" + res.data.retcode];
		}

		return [res.data, null];
	},

	// 
	async uploadFile(tempFilePath) {
		let loginUser = uni.getStorageSync("loginUser");
		if (!loginUser || !loginUser.token) {
			uni.reLaunch({
				url: "/pages/login/login"
			});
		}

		let [error, res] = await uni.uploadFile({
			url: getApp().globalData.domain + "/oss/uploadImageOrVideo",
			filePath: tempFilePath,
			name: "file",
			header: {
				"token": loginUser.token
			}
		});

		if (error || !res) {
			uni.showToast({
				title: "",
				duration: 1000,
			});
			return;
		}

		var data = JSON.parse(res.data);

		if (res.statusCode !== 200) {
			uni.showToast({
				title: "",
				duration: 1000,
			});
			return;
		}
		if (data.retcode !== 0) {
			uni.showToast({
				title: "" + data.retcode,
				duration: 1000,
			});
			// token
			if (data.retcode === 100001 || data.retcode === 100002) {
				uni.reLaunch({
					url: "/pages/login/login"
				});
				uni.setStorageSync("loginUser", null);
				uni.showToast({
					title: "",
					duration: 2000,
				});
			}
			return;
		}

		return [data.body];
	}
}
